@import url(https://fonts.googleapis.com/css?family=Roboto:900);
* {
    box-sizing: border-box;
}

body {
    margin: 0;
    font-family: Roboto, sans-serif;
    background: #f1f1f1;
}

.main-title {
    text-align: center;
    font-size: 35px;
    overflow: hidden;
    margin: 1% 0 1% 0;
}
.navbar {
    background-color: #2671ed !important;
    color: white;
    width: 100%;
    text-align: center;
    overflow: hidden;
}

.header_text {
    font-size: 30px;
    margin: 15px 0px 15px 0px;
}
.container {
    margin: 0 auto;
    width: 80%;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    grid-gap: 10px;
    margin-top: 2%;
}

.item-title {
    font-size: 20px;
    text-align: center;
    margin-bottom: 2%;
}

.container img {
    width: 100%;
    object-fit: cover;
    max-height: 180px;

}

.details-container {
    background-color: white;
    overflow: hidden;
    padding: 2%;
    margin-top: -4px !important;
    border-top: 3px solid #2f2fff;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
}

.flex-container {
    display: flex;
    flex-direction: row;
    margin-bottom: 5%;
    margin-top: 2%;
}

.item1 {
    margin-right: auto;
}

.item2 {
    margin-left: auto;
}

.description {
    margin-bottom: 5%;
    font-family: Arial, Helvetica, sans-serif;
}

.desc-bt {
    color: #000000 !important;
    text-transform: uppercase;
    background: #ffffff;
    padding: 8px;
    border: 2px solid #2f2fff !important;
    border-radius: 6px;
    text-decoration: none;
    display: inline-block;
    -webkit-transition: all 0.3s ease 0s;
    transition: all 0.3s ease 0s;
}

.desc-bt:hover {
    color: #2f2fff !important;
    border-radius: 50px;
    border-color: #2f2fff !important;
    -webkit-transition: all 0.3s ease 0s;
    transition: all 0.3s ease 0s;
}
.button_container {
  text-align: center;
}

button {
  position: relative;
  display: inline-block;
  cursor: pointer;
  outline: none;
  border: 0;
  vertical-align: middle;
  text-decoration: none;
  font-size: inherit;
  font-family: inherit;
}

button.sortB {
  -webkit-transition: all 0.5s cubic-bezier(0.65, -0.25, 0.25, 1.95);
  transition: all 0.5s cubic-bezier(0.65, -0.25, 0.25, 1.95);
  font-weight: 900;
  color: #202121;
  padding: 1.25rem 7rem;
  background: #fff;
  text-transform: uppercase;
}

button.sortB:hover,
button.sortB:focus,
button.sortB:active {
  letter-spacing: 0.125rem;
}
